@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
}

body {
  background: #224941 url('./img/bg.gif') no-repeat center center/cover;
  color: rgb(0, 0, 0);
  font-family: 'Montserrat', sans-serif;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  font-size: 60px;
}

#container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}


#gradient-circle {
  background:  linear-gradient(90deg, #9ebd13 0%, #008552 100%);
  
  text-align: center;
  height: 320px;
  width: 320px;
  
  border-radius: 50%;
  position: absolute;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  
 
}

#text{
  text-align: center;
  
  

  
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

#container-grow {
  
  animation: grow 3s linear forwards;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  

}

@keyframes grow {
  from {
   
    transform:  scale(1);
  }

  to {
    
    transform: scale(1.5);
  }
}

#container-shrink {
  
  animation: shrink 3s linear forwards;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);


}

@keyframes shrink {
  from {
   
    transform: scale(1.5);
  }

  to {
    
    transform: scale(1);
  }
}